@import url("https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@400;700&family=Bellefair&display=swap");
.container {
  position: relative;
  margin: auto;
  width: 1440px;
  height: 900px;
  background: #0b0d17;
  background-image: url("../../assets/destination/background-destination-desktop.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.paragraph {
  position: absolute;
  width: 382px;
  height: 34px;
  left: 166.5px;
  top: 212px;
  display: flex;
  justify-content: space-between;
}
.paragraph h1 {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */
  letter-spacing: 4.725px;
  text-transform: uppercase;
  color: #ffffff;
}
.paragraph span {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */
  letter-spacing: 4.725px;
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
}
.image {
  position: absolute;
  width: 445px;
  height: 445px;
  left: 230px;
  top: 343px;
}
.box {
  position: absolute;
  width: 445px;
  height: 472px;
  left: 832px;
  top: 310px;
}
.box h1 {
  position: absolute;
  height: 115px;
  left: 0.11%;
  right: 38.54%;
  top: calc(50% - 115px / 2 - 107.5px);
  text-transform: uppercase;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 100px;
  line-height: 115px;
  /* identical to box height */

  color: #ffffff;
}
.box p {
  position: absolute;
  height: 128px;
  left: 0%;
  right: 0.22%;
  top: calc(50% - 128px / 2 + 28px);

  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;

  color: #d0d6f9;
}
.box .distance {
  position: absolute;
  left: 0.11%;
  right: 65.51%;
  top: 87.08%;
  bottom: 0%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.distance span {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */

  letter-spacing: 2.3625px;
  text-transform: uppercase;

  color: #d0d6f9;
}
.distance h2 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 32px;
  /* identical to box height */

  text-transform: uppercase;

  color: #ffffff;
}

.box .travel {
  position: absolute;
  left: 49.78%;
  right: 17.87%;
  top: 87.08%;
  bottom: 0%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.travel span {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;

  letter-spacing: 2.3625px;
  text-transform: uppercase;

  color: #d0d6f9;
}
.travel h2 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 32px;
  text-transform: uppercase;
  color: #ffffff;
}
@media (max-width: 800px) {
  .container {
    width: 768px;
    height: 1024px;
    margin: auto;
    background-image: url("../../assets/destination/background-destination-tablet.jpg");
  }
  .paragraph {
    width: 272px;
    height: 24px;
    left: 38.5px;
    top: 136px;
  }
  .paragraph span {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }
  .paragraph h1 {
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }
  .image {
    width: 300px;
    height: 300px;
    left: 234px;
    top: 220px;
  }
  .image img {
    width: 100%;
    height: 100%;
  }
  .box {
    left: 97.5px;
    top: 573px;
    height: 400px;
    width: 573px;
    display: flex;
    flex-direction: column;
  }
  .box h1 {
    width: 218px;
    height: 92px;
    left: 177.5px;
    font-size: 80px;
    line-height: 92px;
    top: 66px;
  }
  .box p {
    font-size: 16px;
    line-height: 28px;
    text-align: center;
    top: 166px;
    height: 84px;
  }
  .box2 {
    position: absolute;
    width: 573px;
    height: 90px;
    top: 299px;
    border-top: 1px solid #383b4b;
  }
  .box2 .distance,
  .box2 .travel {
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    display: block;
  }
  .distance span {
    position: absolute;
    height: 17px;
    left: 11.43%;
    right: 50.87%;
    top: calc(50% - 17px / 2 - 7.5px);
    text-align: center;
  }
  .distance h2 {
    position: absolute;
    height: 32px;
    left: 11.43%;
    right: 50.87%;
    top: calc(50% - 32px / 2 + 29px);
    text-align: center;
  }
  .travel span {
    position: absolute;
    height: 17px;
    left: 51.05%;
    right: 10.03%;
    top: calc(50% - 17px / 2 - 7.5px);
    text-align: center;
  }
  .travel h2 {
    position: absolute;
    height: 32px;
    left: 51.05%;
    right: 10.03%;
    top: calc(50% - 32px / 2 + 29px);
    text-align: center;
  }
}
@media (max-width: 450px) {
  .container {
    width: 375px;
    height: 850px;
    margin: auto;
    background-image: url("../../assets/destination/background-destination-mobile.jpg");
  }
  .paragraph {
    width: 221px;
    height: 19px;
    left: 77.5px;
    top: 88px;
  }
  .paragraph span {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }
  .paragraph h1 {
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }
  .image {
    width: 170px;
    height: 170px;
    left: 103px;
    top: 139px;
  }
  .box {
    width: 327px;
    height: 500px;
    left: 24px;
    top: 335px;
  }
  .box h1 {
    top: 48px;
    left: 70.5px;
    width: 187px;
    height: 64px;
    font-size: 56px;
    line-height: 64px;
    text-align: center;
  }
  .box p {
    top: 113px;
    font-size: 15px;
    line-height: 25px;
  }
  .box2 {
    top: 270px;
    width: 327px;
    height: 154px;
  }
  .distance {
    width: 216px;
    height: 61px;
    left: 52px !important;
  }
  .distance span {
    left: 0%;
    right: 0%;
    top: 0%;
  }
  .distance h2 {
    left: 0%;
    right: 0%;
    top: 50%;
  }
  .travel {
    width: 216px;
    height: 61px;
    top: 126px !important;
    left: 52px !important;
  }
  .travel span {
    left: 0%;
    right: 0%;
    top: 0%;
  }
  .travel h2 {
    left: 0%;
    right: 0%;
    top: 50%;
  }
}
