@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;700&family=Bellefair&display=swap");

.container {
  position: relative;
  width: 1440px;
  height: 900px;
  margin: auto;
  background: #0b0d17;
  background-image: url("../../assets/technology/background-technology-desktop.jpg");
}
.paragraph {
  position: absolute;
  width: 305px;
  height: 34px;
  left: 166.5px;
  top: 212px;
  display: flex;
  justify-content: space-between;
}
.paragraph span {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */
  letter-spacing: 4.725px;
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
}
.paragraph h3 {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */
  letter-spacing: 4.725px;
  color: #ffffff;
  text-transform: uppercase;
}
.box {
  position: absolute;
  width: 444px;
  height: 271px;
  left: 325px;
  top: 383px;
}
.box h3 {
  position: absolute;
  height: 19px;
  left: 0%;
  right: 66.22%;
  top: calc(50% - 19px / 2 - 126px);

  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;

  color: #d0d6f9;
}
.box h1 {
  position: absolute;
  height: 64px;
  left: 0%;
  right: 30.41%;
  top: calc(50% - 64px / 2 - 73.5px);
  text-transform: uppercase;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 64px;

  color: #ffffff;
}
.box p {
  position: absolute;
  height: 160px;
  left: 0%;
  right: 0%;
  top: calc(50% - 160px / 2 + 55.5px);

  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  /* or 178% */

  color: #d0d6f9;
}
.image {
  position: absolute;
  width: 515px;
  height: 527px;
  left: 925px;
  top: 272px;
}
.image2 {
  display: none;
}
.button1 {
  position: absolute;
  left: 165px;
  top: 383px;
  width: 80px;
  height: 80px;
}
.button2 {
  position: absolute;
  width: 80px;
  height: 80px;
  left: 165px;
  top: 495px;
}
.button3 {
  position: absolute;
  width: 80px;
  height: 80px;
  left: 165px;
  top: 607px;
}
.button {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  /* identical to box height */
  text-align: center;
  letter-spacing: 2px;
  color: rgb(255, 255, 255);
  /* mix-blend-mode: normal;
  opacity: 0.25; */
  border: 1px solid #ffffff;
  background-color: inherit;
}

.active {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  cursor: pointer;
  border: none;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  /* identical to box height */
  text-align: center;
  letter-spacing: 2px;
  color: black;
  background-color: white;
}
@media (max-width: 800px) {
  .container {
    width: 768px;
    height: 1024px;
    margin: auto;
    background-image: url("../../assets/technology/background-technology-tablet.jpg");
  }
  .paragraph {
    width: 217px;
    height: 24px;
    left: 38.5px;
    top: 136px;
  }
  .paragraph span {
    position: absolute;
    height: 24px;
    left: 0%;
    right: 89.86%;
    top: calc(50% - 24px / 2);
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }
  .paragraph h3 {
    position: absolute;
    height: 24px;
    left: 17.51%;
    right: 1.84%;
    top: calc(50% - 24px / 2);
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }

  .button1 {
    position: absolute;
    width: 60px;
    height: 60px;
    left: 279px;
    top: 586px;
  }
  .button2 {
    width: 60px;
    height: 60px;
    left: 355px;
    top: 586px;
  }
  .button3 {
    width: 58px;
    height: 58px;
    left: 431px;
    top: 587px;
  }
  .active {
    font-size: 24px;
    line-height: 28px;
  }
  .button {
    font-size: 24px;
    line-height: 28px;
  }
  .box {
    width: 458px;
    height: 237px;
    left: 155px;
    top: 690px;
  }
  .box h3 {
    height: 19px;
    left: 33.84%;
    right: 33.41%;
    top: calc(50% - 19px / 2 - 109px);
    font-size: 16px;
    line-height: 19px;
    letter-spacing: 2.7px;
  }
  .box h1 {
    position: absolute;
    height: 46px;
    left: 0.11%;
    right: 0.11%;
    top: calc(50% - 46px / 2 - 60.5px);
    font-size: 40px;
    line-height: 46px;
    text-align: center;
  }
  .box p {
    position: absolute;
    height: 140px;
    left: 0%;
    right: 0%;
    top: calc(50% - 140px / 2 + 48.5px);
    font-size: 16px;
    line-height: 28px;
    text-align: center;
  }

  .image {
    width: 768px;
    height: 310px;
    left: 0px;
    top: 220px;
  }
  .image1 {
    display: none;
  }
  .image2 {
    display: block;
  }
}
