@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed&display=swap");

.container {
  position: absolute;
  left: 0%;
  right: 35.84%;
  top: 0%;
  bottom: 92.8%;
}

.container ul {
  display: flex;
  justify-content: space-between;
  list-style-type: none;
}

.item {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  cursor: pointer;
  color: #d0d6f9;
}
.item:hover {
  padding-bottom: 0.6rem;
  border-bottom: 3px solid #ffffff;
}

.active {
  padding-bottom: 0.6rem;
  border-bottom: 3px solid #ffffff;
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  cursor: pointer;
  color: #d0d6f9;
}
@media (max-width: 800px) {
  .container {
    width: 285.5px;
    height: 34px;
    left: 143.75px;
  }
  @media (max-width: 450px) {
    .container {
      width: 237.5px;
      height: 28px;
      left: 45px;
    }
  }
}
