@import url("https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;700&display=swap");

.container {
  position: absolute;
  width: 1385px;
  height: 96px;
  left: 55px;
  top: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.image {
  height: 48px;
  width: 48px;
}
a:-webkit-any-link {
  text-decoration: none;
}
.line {
  width: 473px;
  height: 1px;
  left: 125px;
  top: 50px;
  position: absolute;
  background: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
  z-index: 2;
}
.humburger {
  display: none;
}
.close {
  display: none;
}
.nav {
  height: 96px;
  width: 830px;

  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.04);
  backdrop-filter: blur(40.7742px);
}
.list {
  width: 542px;
  height: 19px;
  display: flex;
  justify-content: space-between;
  list-style-type: none;
  margin: 0;
  padding: 0;
}
.item {
  text-transform: uppercase;
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  color: #ffffff;
  cursor: pointer;
  display: flex;
  /* height: 19px; */
}
.item:hover {
  padding-bottom: 2.4rem;
  border-bottom: 3px solid #ffffff;
}

.active {
  padding-bottom: 2.4rem;
  border-bottom: 3px solid #ffffff;
  text-transform: uppercase;
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  letter-spacing: 2.7px;
  color: #ffffff;
  cursor: pointer;
}
