@media (max-width: 800px) {
  .container {
    width: 729px;
    height: 96px;
    left: 39px;
    top: 0px;
  }
  .nav {
    width: 450px;
    height: 96px;
  }
  .list {
    width: 380px;
    height: 18px;
  }
  .item {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    letter-spacing: 2.3625px;
  }
  .line {
    display: none;
  }
}
@media (max-width: 450px) {
  .container {
    width: 340px;
    height: 80px;
    top: 0;
    left: 0px;
    margin: 0px 15px;
  }
  .image {
    width: 40px;
    height: 40px;
  }
  .image img {
    width: 100%;
    height: 100%;
  }

  .close {
    display: block;
    position: absolute;
    left: 195.45px;
    top: 33.95px;
  }
  .humburger {
    display: block;
    width: 24px;
    height: 21px;
  }
  .humburger img {
    width: 100%;
    height: 100%;
  }

  .nav {
    display: none;
    position: absolute;
    width: 254px;
    height: 667px;
    left: 121px;
    top: 0px;
    z-index: 1;
    background: rgba(255, 255, 255, 0.04);
    backdrop-filter: blur(40.7742px);
  }
  .list {
    width: 130px;
    height: 200px;
    flex-direction: column;
    position: absolute;
    top: 118px;
    left: 50px;
  }
  .item,
  .active {
    width: fit-content;
  }

  .item:hover,
  .active {
    padding-bottom: 0.8rem;
    border-bottom: 1px solid #ffffff;
  }
}
