.button {
  width: 15px;
  height: 15px;
  border-radius: 50%;

  mix-blend-mode: normal;
  opacity: 0.25;
  cursor: pointer;
}
.active,
.button:hover {
  background: #ffffff;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background: #ffffff;
  mix-blend-mode: normal;
  cursor: pointer;
}
.button:hover {
  background-color: #ffffff;
}
.list {
  list-style-type: none;
  mix-blend-mode: normal;
  opacity: 0.25;
  width: 15px;
  height: 15px;
}

.icon {
  position: absolute;
  width: 132px;
  height: 15px;
  left: 167px;
  top: 791px;
  display: flex;
  justify-content: space-between;
}
@media (max-width: 800px) {
  .icon {
    width: 88px;
    height: 10px;
    left: 340px;
    top: 442px;
  }
}
@media (max-width: 450px) {
  .icon {
    width: 88px;
    height: 10px;
    left: 144px;
    top: 394px;
  }
}
