@import url("https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed:wght@400;700&family=Bellefair&display=swap");

.container {
  position: relative;
  width: 1440px;
  height: 900px;
  margin: auto;
  background: #0b0d17;
  background-image: url("../../assets/crew/background-crew-desktop.jpg");
}

.paragraph {
  position: absolute;
  width: 286px;
  height: 34px;
  left: 166.5px;
  top: 212px;
  display: flex;
  justify-content: space-between;
}
.paragraph span {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */
  letter-spacing: 4.725px;
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
}
.paragraph h3 {
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */
  letter-spacing: 4.725px;
  text-transform: uppercase;
  color: #ffffff;
}
.box h2 {
  position: absolute;
  width: 214px;
  height: 37px;
  left: 165.5px;
  top: 400px;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  /* identical to box height */
  text-transform: uppercase;
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.5;
}
.box h1 {
  position: absolute;
  width: 488px;
  height: 64px;
  left: 165.5px;
  top: 452px;

  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 56px;
  line-height: 64px;
  text-transform: uppercase;

  color: #ffffff;
}
.box p {
  position: absolute;
  width: 444px;
  height: 128px;
  left: 165px;
  top: 543px;

  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  /* or 178% */

  color: #d0d6f9;
}
.image {
  position: absolute;
  width: 568.07px;
  height: 712px;
  left: 734.93px;
  top: 198px;
}
@media (max-width: 800px) {
  .container {
    width: 768px;
    height: 1024px;
    margin: auto;
    background-image: url("../../assets/crew/background-crew-tablet.jpg");
  }
  .paragraph {
    width: 204px;
    height: 24px;
    left: 38.5px;
    top: 136px;
    display: block;
  }
  .paragraph span {
    position: absolute;
    height: 24px;
    left: 0%;
    right: 89.22%;
    top: calc(50% - 24px / 2);
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }
  .paragraph h3 {
    position: absolute;
    height: 24px;
    left: 18.63%;
    right: 1.47%;
    top: calc(50% - 24px / 2);
    font-size: 19px;
    line-height: 24px;
    letter-spacing: 3.375px;
  }

  .box {
    position: absolute;
    width: 458px;
    height: 232px;
    left: 155px;
    top: 220px;
  }

  .box h2 {
    position: absolute;
    height: 28px;
    left: 32.42%;
    right: 32.42%;
    top: calc(50% - 28px / 2 - 77px);
    width: auto;
    font-size: 24px;
    line-height: 28px;
    /* identical to box height */
    text-align: center;
  }
  .box h1 {
    position: absolute;
    height: 46px;
    left: 0.11%;
    right: 0.11%;
    top: calc(50% - 46px / 2 - 32px);
    width: auto;
    font-size: 40px;
    line-height: 46px;
    text-align: center;
  }
  .box p {
    position: absolute;
    height: 84px;
    width: auto;
    left: 0%;
    right: 0%;
    top: calc(50% - 84px / 2 + 49px);
    font-size: 16px;
    line-height: 28px;
    text-align: center;
  }
  .image {
    width: 368.85px;
    height: 532px;
    left: 200px;
    top: 492px;
  }
  .image img {
    width: 100%;
    height: 100%;
  }
}

@media (max-width: 450px) {
  .container {
    margin: auto;
    width: 375px;
    height: 710px;
    margin: auto;
    background-image: url("../../assets/crew/background-crew-mobile.jpg");
  }
  .paragraph {
    width: 166px;
    height: 19px;
    left: 105px;
    top: 88px;
  }
  .paragraph span {
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 2.7px;
    height: 19px;

    right: 89.76%;
    top: calc(50% - 19px / 2);
  }
  .paragraph h3 {
    font-size: 15px;
    line-height: 19px;
    letter-spacing: 2.7px;
    height: 19px;
    left: 19.88%;
    right: 1.2%;
    top: calc(50% - 19px / 2);
  }
  .image {
    width: 177.12px;
    height: 222px;
    left: 98.62px;
    top: 139px;
  }
  .line {
    position: absolute;
    width: 327px;
    height: 1px;
    left: 24px;
    top: 361px;
    background: #383b4b;
  }
  .box {
    width: 327px;
    height: 170px;
    left: 24px;
    top: 436px;
  }
  .box h2 {
    height: 18px;
    left: 33.64%;
    right: 33.64%;
    font-size: 16px;
    line-height: 18px;
  }
  .box h1 {
    font-size: 24px;
    line-height: 28px;
    height: 28px;
    left: 0%;
    right: 0%;
  }
  .box p {
    font-size: 15px;
    line-height: 25px;
  }
}
