@import url("https://fonts.googleapis.com/css2?family=Barlow&family=Barlow+Condensed&family=Bellefair&display=swap");

.container {
  margin: auto;
  position: relative;
  width: 1440px;
  height: 900px;
  background-color: #0b0d17;
  background-image: url("../assets/home/background-home-desktop.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.home_paragraph {
  position: absolute;
  width: 450px;
  height: 382px;
  left: 165px;
  top: 387px;
}

/* .line {
  position: absolute;
  width: 70px;
  height: 3px;
  left: 752px;
  top: 133px;

  background: #ffffff;
} */
.home_paragraph h2 {
  position: absolute;
  height: 34px;
  left: 0.11%;
  right: 15.67%;
  top: calc(50% - 34px / 2 - 174px);
  text-transform: uppercase;
  font-family: "Barlow Condensed";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  /* identical to box height */

  letter-spacing: 4.725px;

  color: #d0d6f9;
}
.home_paragraph h1 {
  position: absolute;
  height: 172px;
  left: 0%;
  right: 1.11%;
  top: calc(50% - 172px / 2 - 47px);
  text-transform: uppercase;
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 150px;
  line-height: 172px;
  /* identical to box height */

  color: #ffffff;
}

.home_paragraph p {
  position: absolute;
  height: 128px;
  left: 0.11%;
  right: 1.22%;
  top: calc(50% - 128px / 2 + 127px);

  font-family: "Barlow";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 32px;
  /* or 178% */

  color: #d0d6f9;
}

.home_circle {
  position: absolute;
  width: 450px;
  height: 450px;
  left: 913px;
  top: 407px;
}

.circle {
  position: absolute;
  left: 19.56%;
  right: 19.56%;
  top: 19.56%;
  bottom: 19.56%;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;

  background: #ffffff;
}
.circle:hover {
  box-shadow: 0px 0px 5rem rgb(255, 255, 255);
}

.circle h1 {
  font-family: "Bellefair";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 37px;
  /* identical to box height */

  letter-spacing: 2px;
  text-transform: uppercase;

  color: #0b0d17;
}
@media (max-width: 800px) {
  .container {
    margin: auto;
    width: 768px;
    height: 1024px;
    background-image: url("../assets/home/background-home-tablet.jpg");
  }
  .home_paragraph {
    width: 450px;
    height: 334px;
    left: 159px;
    top: 202px;
  }
  .home_paragraph h2 {
    height: 24px;
    left: 20%;
    right: 19.78%;
    top: calc(50% - 24px / 2 - 155px);

    font-size: 20px;
    line-height: 24px;
    /* identical to box height */

    text-align: center;
    letter-spacing: 3.375px;
  }
  .home_paragraph h1 {
    height: 150px;
    line-height: 150px;
  }
  .home_paragraph p {
    height: 112px;
    left: 0.67%;
    right: 0.67%;
    top: calc(50% - 112px / 2 + 111px);

    font-size: 16px;
    line-height: 28px;
    /* or 175% */

    text-align: center;
  }
  .home_circle {
    width: 242px;
    height: 242px;
    left: 263px;
    top: 692px;
  }
  .circle {
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
  }
}
@media (max-width: 450px) {
  .container {
    margin: auto;
    width: 375px;
    height: 667px;
    left: 0px;
    top: 0px;
    background-image: url("../assets/home/background-home-mobile.jpg");
  }
  .home_paragraph {
    width: 327px;
    height: 276px;
    left: 24px;
    top: 112px;
  }
  .home_paragraph h2 {
    height: 19px;
    left: 16.82%;
    right: 16.82%;
    top: calc(50% - 19px / 2 - 128.5px);
    font-family: "Barlow Condensed";
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    text-align: center;
    letter-spacing: 2.7px;
  }
  .home_paragraph h1 {
    height: 100px;
    left: 0%;
    right: 0%;
    top: calc(50% - 100px / 2 - 53px);
    font-family: "Bellefair";
    font-style: normal;
    font-weight: 400;
    font-size: 80px;
    line-height: 100px;
    /* identical to box height, or 125% */
    text-align: center;
  }
  .home_paragraph p {
    position: absolute;
    height: 125px;
    left: 0%;
    right: 0%;
    top: calc(50% - 125px / 2 + 75.5px);
    font-family: "Barlow";
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 25px;
    text-align: center;
  }
  .home_circle {
    width: 150px;
    height: 150px;
    left: 113px;
    top: 469px;
  }
  .circle h1 {
    font-weight: 400;
    font-size: 20px;
    line-height: 23px;
    letter-spacing: 1.25px;
  }
}
